.category {
  &__photos {
    @include page-grid;

    margin-bottom: 16px;
  }

  &__link {
    position: relative;
    &--wide {
      @include on-tablet {
        grid-column: span 4;
      }

      @include on-desktop {
        grid-column: span 8;
      }
    }

    &--square {
      height: 0;
      padding-bottom: 100%;

      @include on-tablet {
        grid-column: span 2;
      }

      @include on-desktop {
        grid-column: span 4;
      }
    }
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;

    @include hover(box-shadow, $photo-shadow);
  }

  &__title {
    margin: 0;
    font-size: 18px;
    line-height: 140%;

    cursor: pointer;

    @include hover(scale, 1.02);

    @include on-tablet {
      font-size: 24px;
    }
  }
}
